import { Falha } from './../falha/falha';
import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { EMPTY } from "rxjs";
import { Observable } from "rxjs";
import { take } from "rxjs/operators";
import { environment } from "src/environments/environment";
import { Plano } from "./plano";

const API = environment.apiUrl;

@Injectable({
    providedIn: 'root'
})

export class PlanoService{
    constructor(
        private http: HttpClient
    ){}


    newPlano(plano: Plano){
        return this.http.post(`${API}plano`, plano).pipe(take(1));
    }

    getAllPlano(): Observable<Plano[]>{
        return this.http.get<Plano[]>(`${API}plano`)        
    }

    getIdEquals(): Observable<Falha[]>{
        return this.http.get<Falha[]>(`${API}plano/falha`)        
    }

    getPlanoById(id){
        if(!id) return EMPTY;
        return this.http.get<Plano[]>(`${API}plano/find/${id}`).pipe(take(1));
    }

    updateStatus(id, status){
        const alterValue = [id, status]
        return this.http.put(`${API}plano/find/${alterValue[0]}`, alterValue).pipe(take(1));
    }
}