import { CadPlanoComponent } from './plano/cad-plano/cad-plano.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LoginComponent } from './core/login/login.component';
import { HomeComponent } from './home/home.component';
import { LoginGuard } from './core/auth/login.guard';
import { AuthGuard } from './core/auth/auth.guard';
import { InicioComponent } from './home/inicio/inicio.component';
import { CadFalhaComponent } from './falha/cad-falha/cad-falha.component';
import { ListaFalhaComponent } from './falha/lista-falha/lista-falha.component';
import { ListaPlanoComponent } from './plano/lista-plano/lista-plano.component';

const routes: Routes = [
    {
        path: '',
        pathMatch: 'full',
        redirectTo: 'login',
    },
    {
        path: 'login',
        component: LoginComponent,
        canActivate: [LoginGuard]
    },
    {
        path: 'home',
        component: HomeComponent,
        canActivate: [AuthGuard],
        children: [
            {
                path: '',
                component: InicioComponent
            },
            {
                path: 'cadastrar/falha',
                component: CadFalhaComponent
            },
            {
                path: 'listar/falhas',
                component: ListaFalhaComponent
            },
            {
                path: 'cadastrar/plano',
                component: CadPlanoComponent
            },
            {
                path: 'cadastrar/plano/:id',
                component: CadPlanoComponent
            },
            {
                path: 'lista/plano',
                component: ListaPlanoComponent
            }
        ]
    }
]

@NgModule({
    imports: [ 
        RouterModule.forRoot(routes, { useHash: true, relativeLinkResolution: 'legacy' } ) 
    ],
    exports: [ RouterModule ]
})
export class AppRoutingModule{
    
}