import { FalhaService } from './../../falha/falha.service';
import { Falha } from './../../falha/falha';
import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { map, switchMap } from "rxjs/operators";
import { ConfirmationDialogService } from "src/app/compartilhado/componentes/confirmation-dialog/confirmation-dialog.service";
import { Login } from "src/app/core/login/login/login";
import { LoginService } from "src/app/core/login/login/login.service";
import { Plano } from "../plano";
import { PlanoService } from "../plano.service";

@Component({
    selector: 'app-cad-plano',
    templateUrl: './cad-plano.component.html',
    styleUrls: ['./cad-plano.component.css'],
    providers: [ConfirmationDialogService]
})

export class CadPlanoComponent implements OnInit {

    novoPlanoForm: FormGroup
    login: Login

    constructor(
        public confirmationDialogService: ConfirmationDialogService,
        private formBuilder: FormBuilder,
        private router: Router,
        private route: ActivatedRoute,
        private loginService: LoginService,
        private PlanoService: PlanoService,
        private FalhaService: FalhaService
    ) { }

    ngOnInit() {

        this.loginService.getUser().subscribe(result => {
            this.login = result;
        })

        this.novoPlanoForm = this.formBuilder.group({
            fk_id_falha: [null, [Validators.required, Validators.minLength(1)]],
            plano_acao: [null, [Validators.required, Validators.minLength(1)]],
            responsavel: [null, [Validators.required, Validators.minLength(1)]],
            objetivo: [null, [Validators.required, Validators.minLength(1)]],
            criacao_data: [null, [Validators.required, Validators.minLength(1)]],
            o_que: [null, [Validators.required, Validators.minLength(1)]],
            por_que: [null, [Validators.required, Validators.minLength(1)]],
            onde: [null, [Validators.required, Validators.minLength(1)]],
            quem: [null, [Validators.required, Validators.minLength(1)]],
            quando: [null, [Validators.required, Validators.minLength(1)]],
            como: [null, [Validators.required, Validators.minLength(1)]],
            quanto: [null, [Validators.required, Validators.minLength(1)]],
            status_plano: ['A fazer', [Validators.required, Validators.minLength(1)]],
            gravidade: [null, [Validators.required, Validators.minLength(1)]],
            urgencia: [null, [Validators.required, Validators.minLength(1)]],
            tendencia: [null, [Validators.required, Validators.minLength(1)]],
            prioridade: [null, [Validators.required, Validators.minLength(1)]],
        })

        this.getDataAtual()

        this.route.params
            .pipe(
                map((params: any) => params['id']),
                switchMap(id => this.FalhaService.getIdFalha(id))

            )
            .subscribe(falha => this.preencherEvento(falha)
            );

    }

    getDataAtual() {
        let data = new Date(),
            dia = data.getDate().toString(),
            diaF = (dia.length == 1) ? '0' + dia : dia,
            mes = (data.getMonth() + 1).toString(), //+1 pois no getMonth Janeiro começa com zero.
            mesF = (mes.length == 1) ? '0' + mes : mes,
            anoF = data.getFullYear(),
            hora = data.getHours(),
            minutos = data.getMinutes()

        let dataNotificacao = anoF + "-" + mesF + "-" + diaF + " " + hora + ":" + minutos

        this.novoPlanoForm.patchValue({ criacao_data: dataNotificacao })
    }

    addNovoPlano() {

        this.novoPlanoForm.patchValue({ prioridade: Number(this.novoPlanoForm.value.gravidade) + Number(this.novoPlanoForm.value.tendencia) + Number(this.novoPlanoForm.value.urgencia) })
        let elementoIncidente = <HTMLInputElement>document.getElementById('falha_ocorrida')

        console.log(this.novoPlanoForm.value)

        this.confirmationDialogService.confirm('Por favor, confirme...', `O Plano de Ação "${this.novoPlanoForm.value.plano_acao}" será adicionado a falha "${elementoIncidente.value}". Deseja prosseguir?`)
            .then((confirmed) => {
                if (confirmed == true) {
                    const novoPlano = this.novoPlanoForm.getRawValue() as Plano;
                    this.PlanoService.newPlano(novoPlano).subscribe(
                        success => {
                            alert('Plano de Ação cadastro com sucesso!')
                            this.formReset()
                            /* this.newFormPlano(); */
                        },
                        error => {
                            alert('Não foi possível cadastrar o plano de ação. Tente novamente.')
                        }
                    )
                }
                else if (confirmed == false) {

                }
            }
            )
            .catch(() => console.log('Request denied.'));

    }

    preencherEvento(falha: Falha) {
        this.novoPlanoForm.patchValue({
            fk_id_falha: falha[0].id
        })

        let elementoFalha = <HTMLInputElement>document.getElementById('descricao_falha')
        elementoFalha.value = falha[0].descricaoFalha
        let elementoOcorrencia = <HTMLInputElement>document.getElementById('falha_ocorrida')
        elementoOcorrencia.value = falha[0].causaFalha

    }

    formReset() {
        this.novoPlanoForm.reset();
        this.router.navigate(['/home/cadastrar/plano']);
    }

    checkError(field) {
        return {
            'has-danger': this.touchedVerify(field)
        };
    }

    touchedVerify(field) {
        return !this.novoPlanoForm.get(field).valid && this.novoPlanoForm.get(field).touched;
    }

}