import { Component, ElementRef, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ConfirmationDialogService } from 'src/app/compartilhado/componentes/confirmation-dialog/confirmation-dialog.service';
import { Falha } from '../falha';
import { FalhaService } from '../falha.service';

@Component({
  selector: 'app-cad-falha',
  templateUrl: './cad-falha.component.html',
  styleUrls: ['./cad-falha.component.css'],
  providers: [ConfirmationDialogService]
})
export class CadFalhaComponent implements OnInit {


  novaFalhaForm: FormGroup;
  setores = ['Ortopedia', 'Plástica', 'Otorrino', 'Cardíaco', 'GO', 'Neuro', 'Geral']
  salas = ['1', '2', '3', '4', '5', '6', '7', '8', 'GO 1', 'GO 2', 'Emergência']
  causas = ['Atraso de Colaborador', 'Atraso de Paciente', 'Atraso de Documentação', 'Falta de Material', 'Falta de Assinatura', 'Falta de Leito', 'Risco para Paciente', 'Negligência do Processo', 'Tasy']
  falhas = ['Atraso no Processo', 'Cancelamento do Processo', 'Transtorno ao Paciente', 'Risco para Paciente']

  constructor(
    public confirmationDialogService: ConfirmationDialogService,
    private formBuilder: FormBuilder,
    private router: Router,
    private elementRef: ElementRef,
    private FalhaService: FalhaService

  ) { }

  ngOnInit() {

    this.novaFalhaForm = this.formBuilder.group({
      dataFalha: [null, [Validators.required]],
      setorFalha: ['Centro Cirúrgico'],
      salaFalha: [null, [Validators.required, Validators.minLength(1)]],
      descricaoFalha: [null],
      horaInicio: [null, [Validators.required, Validators.minLength(1)]],
      horaFim: [null, [Validators.required, Validators.minLength(1)]],
      duracaoFalha:[null],
      causaFalha: [null, [Validators.required, Validators.minLength(1)]],
      efeitoFalha: [null, [Validators.required, Validators.minLength(1)]],
    })
  }


  addNovaFalha() {

    this.addDuracao()
    console.log(this.novaFalhaForm.value);
    

    this.confirmationDialogService.confirm('Por favor, confirme...', `Você realmente deseja adicionar a falha citada? Após adicionada, não será possível sua exclusão.`)
      .then((confirmed) => {
        if (confirmed == true) {
          const novaFalha = this.novaFalhaForm.getRawValue() as Falha;
          this.FalhaService.novaFalha(novaFalha).subscribe(
            success => {
              alert('Cadastro efetuado com sucesso!')
              this.novaFalhaForm.reset();
            },
            error => {
              alert('Não foi possível cadastrar a falha.')
            }
          )
        }
        else if (confirmed == false) {

        }
      }
      )
      .catch(() => console.log('Request denied.'));
  }

  addDuracao() {
    let h1 = parseFloat(this.novaFalhaForm.value.horaInicio[0] + this.novaFalhaForm.value.horaInicio[1]);
    let m1 = parseFloat(this.novaFalhaForm.value.horaInicio[3] + this.novaFalhaForm.value.horaInicio[4]);
    let h2 = parseFloat(this.novaFalhaForm.value.horaFim[0] + this.novaFalhaForm.value.horaFim[1]);
    let m2 = parseFloat(this.novaFalhaForm.value.horaFim[3] + this.novaFalhaForm.value.horaFim[4]);
    let t1 = (h1 * 60) + m1;
    let t2 = (h2 * 60) + m2;
    if (t1 > t2) {
      h2 = h2 + 24;
      t2 = (h2 * 60) + m2;
    }
    let t3 = t2 - t1;
    let t4 = Math.floor(t3 / 60);
    let t5 = t3 - (t4 * 60);

    let horaFinal
    let minFinal

    if(t4 < 10){
      horaFinal = "" + 0 + t4
    }else{
      horaFinal = t4
    }

    if(t5 < 10){
      minFinal = "" + 0 + t5
    }else{
      minFinal = t5
    }

    let res = horaFinal + "h" + ":" + minFinal + "min"

    this.novaFalhaForm.patchValue({duracaoFalha: res})

  }

  checkError(field) {
    return {
      'has-danger': this.touchedVerify(field)
    };
  }

  formReset() {
    this.novaFalhaForm.reset();
  }

  touchedVerify(field) {
    return !this.novaFalhaForm.get(field).valid && this.novaFalhaForm.get(field).touched;
  }

}
