import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Falha } from '../falha';
import { FalhaService } from '../falha.service';

@Component({
  selector: 'app-lista-falha',
  templateUrl: './lista-falha.component.html',
  styleUrls: ['./lista-falha.component.css']
})
export class ListaFalhaComponent implements OnInit {

  listaFalhaForm: FormGroup
  Falhas: Falha[] = []

  constructor(
    private formBuilder: FormBuilder,
    private FalhaService: FalhaService,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.retornarFalhas()
  }

  retornarFalhas() {
    this.FalhaService.retornarFalha().subscribe(res => { this.Falhas = res })
  }

  addPlanoAcao(id){
    this.router.navigate(['/home/cadastrar/plano', id]);
   //console.log(id)
}

}
